import React from 'react';

// hooks
import { useEventsModule } from './hooks/useEventsModule';

// components
import { Outlet } from 'react-router-dom';

// styles
import './Events.scss';

const Events: React.FC = () => {
  const { eventId, event } = useEventsModule();

  if (!eventId || !event) {
    return null;
  }

  return (
    <div className="events-module ">
      <Outlet />
    </div>
  );
};

export default Events;
